import {
  DownloadOutlined,
  GithubOutlined,
  SaveOutlined,
  SettingOutlined,
  SearchOutlined,
  TagsOutlined,
  UploadOutlined
} from '@ant-design/icons-vue'

const iconComponents = {
  DownloadOutlined,
  GithubOutlined,
  SaveOutlined,
  SettingOutlined,
  SearchOutlined,
  TagsOutlined,
  UploadOutlined
}

export default function (app) {
  Object.keys(iconComponents).forEach(iconName => {
    app.component(
      // Ê≥®ÂÜåÁöÑÂêçÂ≠ó
      iconName,
      // ÁªÑ‰ª∂ÁöÑÂÆûÁé∞
      iconComponents[iconName]
    )
  })
}
