import { message } from 'ant-design-vue'
import websiteConfig from '@/config/websiteConfig'

// stateÔºöÊîæÊï∞ÊçÆÁöÑÂú∞Êñπ
const state = {
  theme: localStorage.getItem('THEME') || 'light', // light or dark
  websiteData:
    JSON.parse(localStorage.getItem('WEBSITECONFIG')) || websiteConfig
}

// gettersÔºöÁêÜËß£‰∏∫ËÆ°ÁÆóÂ±ûÊÄß,Áî®‰∫éÁÆÄÂåñ‰ªìÂ∫ìÊï∞ÊçÆ,ËÆ©ÁªÑ‰ª∂Ëé∑Âèñ‰ªìÂ∫ìÁöÑÊï∞ÊçÆÊõ¥Âä†Êñπ‰æø
const getters = {
  isDark: state => (state.theme === 'dark' ? true : false)
}

// mutationsÔºàÂêåÊ≠•ÔºâÔºö‰øÆÊîπstateÂîØ‰∏ÄÊâãÊÆµ
const mutations = {
  SET_THEME: (state, payload) => {
    state.theme = payload
    localStorage.setItem('THEME', payload)
    document.documentElement.setAttribute('data-theme', payload)
  },
  SET_WEBSITECONFIG: (state, payload) => {
    try {
      state.websiteData = JSON.parse(payload)
      localStorage.setItem('WEBSITECONFIG', payload)
      message.success({ content: 'ÈÖçÁΩÆËÆæÁΩÆÊàêÂäü!', key: 'success' })
    } catch (e) {
      message.error({ content: 'JSONÊ†ºÂºèÊúâËØØ!', key: 'error' })
      console.error(e)
    }
  }
}

// actionsÔºàÂºÇÊ≠•ÔºâÔºö‰π¶ÂÜô‰∏öÂä°ÈÄªËæëË∑üÂ§ÑÁêÜÂºÇÊ≠•
const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
